































var graceChecker = require("@/common/graceChecker.js");
export default {
	data() {
		return {
			password: '',
			newpassword: '',
			confirmpassword: ''
		}
	},
	methods: {
		submit(e) {
			// 
			var rule = [{
				name: "newpassword",
				checkType: "password",
				checkRule: "6,8",
				errorMsg: "请输入包含数字和英文字母的密码或密码长度大于6位"
			}];
			//进行表单检查
			var formData = e.detail.value;
			var checkRes = graceChecker.check(formData, rule);
			// 验证---密码和确认密码是否一致
			if (e.detail.value.newpassword != e.detail.value.confirmpassword) {
				uni.showToast({
					title: '两次密码不一致',
					icon: 'none'
				})
				return false;
			}
			// 如果核对信息填写全部正确 存储用户信息
			if (checkRes) {
				uni.request({
					url: this.apiUrl + 'login/editPassword',
					method: 'POST',
					data: {
						mobile: uni.getStorageSync('userLoginInfo').phone,
						password: this.password,
						newpassword: this.newpassword,
						confirm_password: this.confirmpassword,
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: res => {
						console.log(res);
						if (res.data.code == 0) {
							//uni.setStorageSync('userLoginInfo', res.data.data);
							//uni.setStorageSync('locationShop', res.data.data.shop);
							uni.showToast({
								title: '修改成功',
								icon: 'none',
								success: (res) => {
									setTimeout( () => {
										uni.redirectTo({
											url: '../login/login'
										});
									}, 500);
								}
							});
						}else{
							uni.showToast({
								title:'不能修改密码~',
								icon:'none'
							})
						}
					},
					fail: () => {
						
					},
					complete: () => {}
				});
			} else {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
			}
		},
	}
}
